export default {
	data() {
		return {
			swipers: []
		}
	},
	onLoad() {
		// 页面加载 获取轮播图数据
		this.getSwiper()
	},
	methods: {
		// 获取轮播图的数据
		getSwiper () {
			console.log('获取轮播图数据')
			uni.request({
				url:'../../static/api/lunbotu.json',
				success: res=> {
					console.log(res)
					if(res.data.status !==0){
						return uni.showToast({
							title:'获取数据失败',
							icon: 'none'
						})
					}
					this.swipers = res.data.message
				}
			})
		}
	}
}
